import { Button, Modal } from 'antd'
import React, { useState } from 'react'
import ContactUsModal from '../../components/ContactUsModal'
import Footer from '../../components/Footer'
import PageHeader from '../../components/Header'
import Services from '../../components/Services'

export default ({location}) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };
  // fetch('https://api.ipregistry.co/?key=tryout')
  // .then(function (response) {
  //     return response.json();
  // })
  // .then(function (payload) {
  //     console.log(payload.location.country.name + ', ' + payload.location.city);
  // });
  const activeKey = location.hash ? location.hash.substring(1) : "";
  return (
    <div>
      <ContactUsModal isModalOpen={isModalOpen} handleOk={handleOk} handleCancel={handleCancel}/>
      <PageHeader selectedKey="services" isTransparentHeader={false} />
      <Services showContactUs={showModal} activeKey={activeKey} />
      <Footer />
    </div>
  )
}
